.sidebar {
    background: $color-1;
    font-family: $font-1;
    height: 100%;
    min-height: $minHeight;
    width: 264px;
    border-radius: $borderRadius;
    position: relative;
    display: grid; //mieux pour la r√©partition
    grid-template-columns: 1fr;
    grid-template-rows: 250px 1fr 108px;
    margin-left: 1rem;
    grid-template-areas: 
      "1"
      "2"
      "3";
  
    .id {
      grid-area: 1;
      text-align: center;
      user-select: none; //pas pouvoir clicker dessus
      position: relative;
  
      .idContent {
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: 80%;
  
        img {
          border-radius: 50%;
          width: 100%;
          box-shadow: 0 0 0 4px $color-3, 0 0 0 8px $color-4, 0 0 0 12px $color-2;
        }

        h3 {
          font-size: 1.3rem;
          letter-spacing: .2rem;
          margin-top: 1.5rem;
          color: $color-5;
        }
      }
    }

    .navigation {
      grid-area: 2;
      height: 100%;
      padding: 2rem 1rem;
  
      ul {
        height: 100%;
        display: grid; //repartission en grille
      
        li {
          padding: 1rem 0 1rem .4rem;
          font-size: 1.3rem;
          position: relative;
  
          a {
          color: $color-5;
          }
          
          .navActive {
            color: $color-4;
          }
  
          i {
            position: absolute;
            font-size: 0.95rem;
            transform: translateY(3px);
            transition: .2s ease;
          }
          span {
            margin-left: 2rem;
          }
        }
      }
    }
    .socialNetwork {
      grid-area: 3;
      
      ul {
        display: flex;
        width: 264px;
        padding: 0 50px;
        justify-content:space-around;
                
  
        li {
          font-size: 1.5rem;
          background: $color-5;
          color: $color-1;
          border-radius: 50%;
          padding: 0.1rem 0.3rem;
          transition: .3s;
          cursor: pointer;
         
          
          img{
            width: 100%;
            align-items: center;
          }

          a:visited {
            color: $color-1;
          }
       
          &:hover {
            background: $color-4;
          }
        }
      }

      .signature {
        margin-top: 1.5rem;
        text-align: center;
        letter-spacing: 0.1rem;
        font-size: 1rem;
        color: $color-5
      }
    }
  }
  
  @media screen and (max-width: 850px){
    .sidebar {
      display: flex;
      min-height: 50px;
      width: 100%;
      margin-bottom: .2rem;
      margin-left :auto;
      margin-right :auto;
  
      .id, .socialNetwork {
        display: none;
      }
      .navigation {
        width: 100%;
        padding: .1rem 1rem;
  
        ul {
          display: flex;
          justify-content: space-between;
  
          span {
            margin-left: 1.5rem !important;
          }
        }
      }
    }
  }
  @media screen and (max-width:580px){
    .sidebar {
      ul {
        justify-content: space-around !important;
  
        li {
          padding: 1rem 0 !important;
        }
        i {
          font-size: 1.4rem !important;
          transform: translateY(-3px) !important;
        }
        span {
          display: none;
        }
      }
    }
  }