.formation {
    .formationContent {
        background: $color-1;
        min-height: $minHeight;
        width: 100%;
        border-radius: $borderRadius;
        position: relative;
        overflow: hidden;
        margin-left: 0.5rem;
        margin-right: 1rem;
    
        h1 {
            font-size: 2rem;
            text-align: center;
            font-family: $font-2;
            color: $color-3;
            text-transform: uppercase;
            text-shadow: 3px 6px 2px $black;
            margin-top: 1rem;
        }
    }
}
@media screen and (max-width: 850px) {
    
}
@media screen and (max-width: 580px) {
    .homeContent {
       
        h1 {
            font-size: 3rem !important;
        }
       
    }
}
